import { API, services } from "@miq/adminjs";
import { getService } from "@miq/utils";

export const departmentServices = {
    path: "departments/",
    list(params) {
        return getService(API, this.path, params);
    },
    listPage(url) {
        return getService(API, url);
    },
    get(depSlug, params) {
        return getService(API, `${this.path}${depSlug}/`, params);
    },
    post(values) {
        return services.post(`${this.path}`, values);
    },
    patch(depSlug, values, oldValues) {
        return services.patch(`${this.path}${depSlug}/`, values, oldValues);
    },
    delete(depSlug) {
        return services.delete(`${this.path}${depSlug}/`);
    },
};
