import { useEffect, useState } from "react";
import PropTypes from "prop-types";
import { NavLink } from "react-router-dom";

import { AdminView } from "@miq/adminjs";
import { addForwardSlash } from "@miq/utils";
import { Avatar, ItemList } from "@miq/components";

import { departmentServices } from "./utils";

const propTypes = {};

export default function DepartmentListView(props) {
    const { compSlug } = props.match.params;
    const [data, setData] = useState({ results: [] });

    useEffect(() => {
        departmentServices
            .list({ company: compSlug })
            .then((data) => {
                setData(data);
                // console.log(data);
            })
            .catch((err) => {});
    }, [compSlug]);

    return (
        <AdminView title="Departments" className="department-list-view">
            <ItemList
                items={data.results}
                renderItem={(department = { user: {} }) => {
                    const url = addForwardSlash(props.match.url);

                    return (
                        <NavLink key={department.slug} to={`${url}${department.slug}/`}>
                            <div className="department-list-item">{department.name}</div>
                        </NavLink>
                    );
                }}
                pagination={{
                    count: data.count,
                    next: data.next,
                    previous: data.previous,
                    onPreviousClick: () => {},
                    onNextClick: () => {},
                }}
            />
        </AdminView>
    );
}

DepartmentListView.propTypes = propTypes;
