import { AdminRoute } from "@miq/adminjs";
import { Suspense, lazy } from "react";
import { Switch } from "react-router-dom";

import { Loading } from "@miq/components";

import DepartmentListView from "./ListView";

const DepartmentUpdateView = lazy(() => import("./UpdateView"));

export default function DepartmentRoutes(props) {
    const { path } = props.match;

    return (
        <Suspense fallback={<Loading />}>
            <Switch>
                <AdminRoute
                    path={`${path}:depSlug/`}
                    component={DepartmentUpdateView}
                    requiredPerms={["miq_hrm.change_department"]}
                />
                <AdminRoute path={path} component={DepartmentListView} requiredPerms={["miq_hrm.view_department"]} />
            </Switch>
        </Suspense>
    );
}
